@keyframes preloader-inside-white {
  0% {
      -webkit-transform: scale(0, 0);
      -moz-transform: scale(0, 0);
      -ms-transform: scale(0, 0);
      -o-transform: scale(0, 0);
      transform: scale(0, 0);
  }
  100% {
      -webkit-transform: scale(1, 1);
      -moz-transform: scale(1, 1);
      -ms-transform: scale(1, 1);
      -o-transform: scale(1, 1);
      transform: scale(1, 1);
  }
}

@keyframes preloader-inside-red {
  0% {
      -webkit-transform: scale(0, 0);
      -moz-transform: scale(0, 0);
      -ms-transform: scale(0, 0);
      -o-transform: scale(0, 0);
      transform: scale(0, 0);
  }
  30% {
      -webkit-transform: scale(0, 0);
      -moz-transform: scale(0, 0);
      -ms-transform: scale(0, 0);
      -o-transform: scale(0, 0);
      transform: scale(0, 0);
  }
  100% {
      -webkit-transform: scale(1, 1);
      -moz-transform: scale(1, 1);
      -ms-transform: scale(1, 1);
      -o-transform: scale(1, 1);
      transform: scale(1, 1);
  }
}

.preloader {
  display: none;
  align-items: center;
  justify-content: center;
  max-width: calc(100% - 140px);
  margin: auto;
  height: 100%;
  z-index: 10;
  padding: 20px;
}

.preloader_active {
  display: flex;
}

.preloader__container {
  width: 100px;
  height: 100px;
  position: relative;
}

.preloader__round {
  width: 100px;
  height: 100px;
  position: absolute;
  top: 0;
  left: 0;
  background: #2be080;
  border-radius: 50px;
}

.preloader__round::after,
.preloader__round::before {
  content: '';
  width: 100px;
  height: 100px;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50px;
}

.preloader__round::after {
  background: white;
  animation: preloader-inside-white 1s ease-in-out infinite;
}

.preloader__round::before {
  z-index: 10;
  background: #2be080;
  animation: preloader-inside-red 1s ease-in-out infinite;
}

@media screen and (min-width: 601px) and (max-width: 1000px) {
  .preloader {
    max-width: calc(100% - 60px);
    padding: 12px;
  }
}

@media screen and (max-width: 600px) {
  .preloader {
    max-width: calc(100% - 28px);
    padding: 5px;
  }

  .preloader__container {
    width: 50px;
    height: 50px;
  }

  .preloader__round {
    width: 50px;
    height: 50px;
    border-radius: 50%;
  }

  .preloader__round::after,
  .preloader__round::before {
    width: 50px;
    height: 50px;
    border-radius: 50%;
  }

}